import reservationApi from '../../api/reservation'

const state = {
  guests: [],
  steps: 1
}

const mutations = {
  setGuests(state, guests) {
    state.guests = guests
  },
  setSteps(state, steps) {
    state.steps = steps
  }
}

const actions = {
  async loadGuests ({ commit }, reservationCode) {
    const reservation = await reservationApi.retrieveReservation(reservationCode)
    // console.log(JSON.stringify(reservation))

    // need to show error dialog here
    if (!reservation) {
      // need to show error dialog here
      console.error('no reservation')
      return
    }

    const guests = await reservationApi.retrieveGuests(reservationCode)

    if (guests.length < reservation.guestCount) {
      let i = guests.length

      for (i; i < reservation.guestCount; i++) {
        guests.push({ 
          reservationCode: reservationCode, 
          guestNo: i, 
          firstName: '', 
          lastName: '',
          lastUpdateOn: null
        })
      }
    }

    commit('setGuests', guests)
    commit('setSteps', guests.length)

/*
    let emptyGuests = [];
    let i
    for (i = 0; i < reservation.guestCount; i++) {
      emptyGuests[i] = { 
        reservationCode: reservationCode, 
        guestNo: i + 1, 
        firstName: '', 
        lastName: '',
        lastUpdateOn: null
      }
    }

    commit('setGuests', emptyGuests)
    commit('setSteps', emptyGuests.length)
*/
  },
  async saveGuest ({ commit }, guest) {
    if (guest.lastUpdateOn) {
      guest.lastUpdateOn = Math.round((new Date).getTime() / 1000)
      const reservation = await reservationApi.updateReservationGuest(guest)
      console.log(reservation)
    } else {
      guest.lastUpdateOn = Math.round((new Date).getTime() / 1000)
      const reservation = await reservationApi.createReservationGuest(guest)
      console.log(reservation)
    }
  }
}

const getters = {}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}