export const getReservation = `query GetReservation($reservationCode: String!) {
  getReservation(reservationCode: $reservationCode) {
    reservationCode
    guestCount
    checkinDate
  }
}
`;

export const getReservationGuest = `query GetReservationGuest($reservationCode: String!) {
  getReservationGuest(reservationCode: $reservationCode) {
    reservationCode
    guestNo
    lastName
    firstName
    faceImgName
    faceImgKey
    faceImgBucket
    idImgName
    idImgKey
    idImgBucket
    lastUpdateOn
  }
}
`;

export const createReservationGuest = `mutation createReservationGuest($reservationCode: String!, $guestNo: Int!, $lastName: String, $firstName: String, $faceImgName: String, $faceImgKey: String, $faceImgBucket: String, $idImgName: String, $idImgKey: String, $idImgBucket: String, $lastUpdateOn: Int!) {
  createReservationGuest(input: { reservationCode: $reservationCode, guestNo: $guestNo, lastName: $lastName, firstName: $firstName, faceImgName: $faceImgName, faceImgKey: $faceImgKey, faceImgBucket: $faceImgBucket, idImgName: $idImgName, idImgKey: $idImgKey, idImgBucket: $idImgBucket, lastUpdateOn: $lastUpdateOn }) {
    reservationCode
    guestNo
    lastName
    firstName
    faceImgName
    faceImgKey
    faceImgBucket
    idImgName
    idImgKey
    idImgBucket
    lastUpdateOn
  }
}
`;


export const updateReservationGuest = `mutation updateReservationGuest($reservationCode: String!, $guestNo: Int!, $lastName: String, $firstName: String, $faceImgName: String, $faceImgKey: String, $faceImgBucket: String, $idImgName: String, $idImgKey: String, $idImgBucket: String, $lastUpdateOn: Int!) {
  updateReservationGuest(input: { reservationCode: $reservationCode, guestNo: $guestNo, lastName: $lastName, firstName: $firstName, faceImgName: $faceImgName, faceImgKey: $faceImgKey, faceImgBucket: $faceImgBucket, idImgName: $idImgName, idImgKey: $idImgKey, idImgBucket: $idImgBucket, lastUpdateOn: $lastUpdateOn }) {
    reservationCode
    guestNo
    lastName
    firstName
    faceImgName
    faceImgKey
    faceImgBucket
    idImgName
    idImgKey
    idImgBucket
    lastUpdateOn
  }
}
`;
